	export default {
		data() {
			return {
				orderInfo:{
					goodsData:[],
					addressData:{}
				},//订单信息
				maskState: 0, //优惠券面板显示状态
				desc: '', //备注
				payType: 1, //1微信 2支付宝
				couponList: [
					{
						title: '新用户专享优惠券',
						price: 5,
					},
					{
						title: '庆五一发一波优惠券',
						price: 10,
					},
					{
						title: '优惠券优惠券优惠券优惠券',
						price: 15,
					}
				],
				// addressData: {
				// 	name: '许小星',
				// 	mobile: '13853989563',
				// 	addressName: '金九大道',
				// 	address: '山东省济南市历城区',
				// 	area: '149号',
				// 	default: false,
				// }
			}
		},
		onLoad(option){
			//商品数据
			//let data = JSON.parse(option.data);
			//console.log(data);
 			if(option.orderNo){
				let _this=this;
				this.sendRequest({
					url : "get_create_order",
					data : {orderNo:option.orderNo},
					method : "get",
					success:function (res) {
						if(res.code=='200'){
							_this.orderInfo=res.data;
						}else{
							_this.$api.msg(res.message);
						}
					}
				});
			}
		},
		methods: {
			imgPath:function(path){
				if(path==''||path==null||path=='null'){
					return "/static/errorImage.jpg"				 
				}else if (path!=''&&path.indexOf('http')>=0) {
					return path;				 
				}
				return this.siteHost + path;
			},
			//显示优惠券面板
			toggleMask(type){
				let timer = type === 'show' ? 10 : 300;
				let	state = type === 'show' ? 1 : 0;
				this.maskState = 2;
				setTimeout(()=>{
					this.maskState = state;
				}, timer)
			},
			numberChange(data) {
				this.number = data.number;
			},
			changePayType(type){
				this.payType = type;
			},
			submit(){
				if(!this.orderInfo.goodsData||this.orderInfo.goodsData.length<=0){
					this.$api.msg('下单失败');
					return;
				}
				if(!this.orderInfo.addressData||this.orderInfo.addressData.id<=0){
					this.$api.msg('请选择收货地址');
					return;
				}
				let platform='';







					platform='wx';

				this.orderInfo.platform=platform;//设置提交平台
				let _this=this;
				_this.sendRequest({
					url : 'save_order',
					data : this.orderInfo,
					method : "post",
					success:function (res) {
						if(res.code=='200'){
							uni.navigateTo({
								url: `/pages/money/pay?orderNo=${res.data.orderNo}`
							})
						}else{
							_this.$api.msg('下单失败');
						}
					}
				});  
			},
			stopPrevent(){}
		}
	}
